import React, { ReactNode } from "react";

export interface SkeletonPageProps {
	dataQA: string;
	children?: ReactNode;
	ariaLabel?: string;
}

/**
 * @description This component is to be used with the `<SkeletonLoader />` component to compose a low fidelity
 * representation of the user interface before content appears on the screen.
 * @accessibility
 * `role="status"`
 * `aria-label="page loading"`
 */
const SkeletonPage = ({
	dataQA,
	children,
	ariaLabel,
}: SkeletonPageProps) => (
	<div
		role="status"
		aria-label={ariaLabel || "page loading"}
		data-qa={`skeleton-page-${dataQA}`}
	>
		{children}
	</div>
);

export default SkeletonPage;
